//Detail.js
//   .
import React from "react";
import { useParams, useHistory } from "react-router-dom";
// redux hook .
import { useDispatch, useSelector } from "react-redux";
//      .
import { deleteBucket, deleteBucketFB, updateBucket, updateBucketFB } from "./redux/modules/bucket";

import Button from "@material-ui/core/Button";

const Detail = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const params = useParams();
  const bucket_index = params.index;

  //   
  const bucket_list = useSelector((state) => state.bucket.list);


  return (
    <div>
      <h1>{bucket_list[bucket_index] ? bucket_list[bucket_index].text : ""}</h1>
      <Button
        variant="outlined"
        color="primary"
        onClick={() => {
          dispatch(updateBucket(bucket_index));
          dispatch(updateBucketFB(bucket_list[bucket_index].id));
          history.goBack();
        }}></Button>

      <Button
        variant="outlined"
        color="secondary"
        onClick={() => {
          //   dispatch(); <-     
          // 
          // console.log("  !");
          dispatch(deleteBucket(bucket_index));
          dispatch(deleteBucketFB(bucket_list[bucket_index].id));
          history.goBack();
        }}></Button>

      <Button
        variant="outlined"
        color="success"
        onClick={() => {
          history.goBack();
        }}></Button>
    </div>
  );
};

export default Detail;