import React from "react";
import styled from "styled-components";
import { Route, Switch } from "react-router-dom";
// useDispatch !
import { useDispatch, useSelector } from "react-redux";
//  !
import { addBucket, loadBucketFB, addBucketFB } from "./redux/modules/bucket";

// BucketList  import .
// import [ ] from [  ];
import BucketList from "./BucketList";
import Detail from "./Detail";
import NotFound from "./NotFound";
import Progress from "./Progress";
import Spinner from "./Spinner";
import {db} from "./firebase";
import { collection, doc,  getDoc, getDocs, addDoc, updateDoc, deleteDoc } from "firebase/firestore";


function App() {

  const text = React.useRef(null);
  // useHistory   ? :)
  const dispatch = useDispatch();
  const is_loaded = useSelector(state => state.bucket.is_loaded);

  React.useEffect( () => {
    dispatch(loadBucketFB());
  }, []);

    //  
    // const docRef = doc(db, "bucket", "7tU616y5QtckRCDKr4Mk");
    // deleteDoc(docRef);

    // //  
    // const docRef = doc(db, "bucket", "7tU616y5QtckRCDKr4Mk");
    // // updateDoc(docRef, {completed: true});
    
    
    //  
    // addDoc(collection(db, "bucket"), {tet: "new", completed: false});


    //  
    // const query = await getDocs(collection(db, "bucket"));
    // console.log(query);
    // query.forEach((doc) => {
    //   console.log(doc.id, doc.data());
    // });

  const addBucketList = () => {
    //  !  ? :)
    //   list   .
    //   !
    // setList([...list, text.current.value]);

    // dispatch(createBucket({text: text.current.value, completed: false}));

    dispatch(addBucketFB({text: text.current.value, completed: false}));
    text.current.value="";
  };

  return (
    <div className="App">
      <Container>
        <Title> </Title>
        <Progress/>
        <Line />
        {/*  . */}
        {/* <  [props ]={ (, , , ...)}/> */}
        <Switch>
          {/* <Route
            path="/"
            exact
            render={(props) => <BucketList list={list} />}
          /> */}
          {/*  render  list   !     ! */}
          <Route exact path="/" component={BucketList} />
          <Route exact path="/detail/:index" component={Detail} />
          <Route component={NotFound} />
        </Switch>
      </Container>
      {/*    . */}
      <Input>
        <input type="text" ref={text} />
        <button onClick={addBucketList}></button>
      </Input>
      
      {/*   */}
      {/* <button onClick={() =>{
        window.scrollTo({top:0, left:0, behavior:"smooth"});
      }}> </button> */}

      {!is_loaded && <Spinner/>}

    </div>
  );
}

const Input = styled.div`
  max-width: 350px;
  min-height: 10vh;
  background-color: #fff;
  padding: 16px;
  margin: 20px auto;
  border-radius: 5px;
  border: 1px solid #ddd;
  display: flex;
  & > * {
    padding: 5px;
  }
  & input{
    border: 1px solid #888;
    width: 70%;
    margin-right: 10px;
  }

  & input:focus {
    outline: none;
    border: 1px solid #a3a0a9;
  }

  & button {
    width: 25%;
    color: #fff;
    border: #a3a0a9;
    background: #a3a0a9;
  }

`;

const Container = styled.div`
  max-width: 350px;
  min-height: 60vh;
  background-color: #fff;
  padding: 16px;
  margin: 20px auto;
  border-radius: 5px;
  border: 1px solid #ddd;
`;

const Title = styled.h1`
  color: #a3a0a9;
  text-align: center;
`;

const Line = styled.hr`
  margin: 16px 0px;
  border: 1px dotted #ddd;
`;

export default App;