import React from "react";
import {useHistory} from "react-router-dom";
import Button from "@material-ui/core/Button";

const NotFound = (props) => {
    const history = useHistory();
    return (
        <>
        <h1>  !</h1>
        <p>   <br/>  </p>
        <Button 
        variant="outlined"
        color="primary" 
        onClick={() =>{
            history.goBack();
        }}></Button>
        </>
    );
};

export default NotFound;